import React from 'react';

import {
  CardActions
} from 'rmwc/Card';

import PrimaryKeys from "./PrimaryKeys";
import SecondaryKeys from "./SecondaryKeys";

function Actions(props) {
  return (
    <CardActions className="mdc-calc-keys">
      <SecondaryKeys calc={props.calc} />
      <PrimaryKeys calc={props.calc} />
    </CardActions>
  );
}

export default Actions;



